.App {
    text-align: center;
    display: block;
    overflow: auto;
    height:100%;
  }
  
  h1, h2, h3, p{
    margin-top: 0;
  }
  
  h1{
    display: block;
  }
  
  
  header,footer {
    margin-top: 0;
    background-color: #2C3E50;
    overflow: auto;
    margin-bottom: 0;
    height: 10vh;
  
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  
  ul{
    list-style-type: none;
  }
  
  li{
    display: inline;
    margin: 10px;
  }
  
  a{
    text-decoration: none;
    margin: 0.5vw;
    color: #ECF0F1;
  }
  
  footer{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .page{
    margin: 0;
    height: 60vh;
    display: block;
    overflow: auto;
    background: linear-gradient(
      45deg,
      #2980B9,
      #1ABC9C
    );
    background-repeat: no-repeat;
  }
  
  .profileImg{
    width: 10rem;
    height: 10rem;
    border-radius: 10rem;
  }
  
  .project-container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .img-container {
    color: white;
    text-shadow: 1px 1px black;
    position: relative;
    display: inline-block;
    margin: 20px;
    width: 20vw;
    height: 20vh;
  }
  
  img {
    width: 100%;
    height: 100%;
  }
  
  .img-container .title{
    position: absolute;
    top: 10%;
    left: 24%;
    pointer-events: none;
  }
  
  .img-container .link{
    position: absolute;
    top:30%;
    left: 24%;
  }
  
  .img-container .project-github{
    position: absolute;
    top: 80%;
    left: 24%;
  }
  
  .img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  form{
    margin: auto;
    width: 100%;
  }
  
  form label{
    text-align: center;
    color:black
  }
  
  form input {
    display: block;
    margin: auto;
    width: 20%;
  }
  
  form textarea{
    display: block;
    margin: auto; 
    resize: none;
    width: 20%;
    overflow: hidden;
  }